import React from 'react';
import { useSelector } from 'react-redux';


const Language = ({ language, langnames, langgroups }) => {
    const rus = useSelector(state => state.rus);
    const subtitles = {
        titlelat: ['Latin names:', ' :'],
        titlegr: ['Language groups:', ' :']
    }

    return (<>
        <p className='langtitle'>{rus ? language.name_rus : language.name_eng}</p>
        <div className='innercontainer'>
            <table className='langparams'>
                <tbody>
                    <tr>
                        <td className='langlist langlisttitle'>{subtitles.titlelat[rus]}</td>
                        <td className='langlist'>{langnames.join(', ')}</td>
                    </tr>
                    <tr>
                        <td className='langlist langlisttitle tablenoborder'>{subtitles.titlegr[rus]}</td>
                        <td className='langlist tablenoborder'>{langgroups.join(', ')}</td>
                    </tr>
                </tbody>
            </table>
            {/* <div className='langparams'>
                <p className='langlist langlisttitle'>{subtitles.titlelat[rus]}</p>
                <p className='langlist'>{langnames.join(', ')}</p>
                <p className='langlist langlisttitle'>{subtitles.titlegr[rus]}</p>
                <p className='langlist'>{langgroups.join(', ')}</p>
            </div> */}
            <p className='langinfo'>{rus ? language.description_rus : language.description_eng}</p>
        </div>
    </>);
};

export default Language;