import { Routes, Route, BrowserRouter, Outlet, NavLink, Navigate } from 'react-router-dom';
import React, { useEffect } from 'react';
import './style/style.css';
import { Provider } from 'react-redux';
import { createStore } from 'redux';
import Langlist from './pages/Langlist';
import { persistStore, persistReducer } from "redux-persist";
import storage from 'redux-persist/lib/storage';
import MainMenu from './pages/MainMenu';
import ContentField from './pages/ContentField';

const initialState = {
  rus: 0,
  upcase: true,
  serif: false,
  italic: true,
  locallang: "ru",
  panCyrSort: true,
  currentLang: null,
  highlightedLang: null,
  highlightedSelect: { languages: null, id: '' },
  currentPage: null // 1-Pancyrillic, 2-Alphabet, 3-Definitions
}

const persistConfig = {
  key: 'root',
  storage: storage,
  whitelist: ['upcase', 'serif', 'italic', 'panCyrSort']
}

export const switchersReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'toggleRuEn': {
      return { ...state, rus: action.payload };
    }
    case 'toggleCase':
      return { ...state, upcase: !state.upcase };
    case 'toggleSerif':
      return { ...state, serif: !state.serif };
    case 'toggleItalic':
      return { ...state, italic: !state.italic };
    case 'switchLang':
      return { ...state, locallang: action.payload };
    case 'switchCurrentLang':
      return { ...state, currentLang: action.payload };
    case 'switchHighlightedLang':
      return { ...state, highlightedLang: action.payload };
    case 'selectHighlights':
      return { ...state, highlightedSelect: action.payload };
    case 'switchPage':
      return { ...state, currentPage: action.payload };
    case 'switchPanCyrSort':
      return { ...state, panCyrSort: !state.panCyrSort };
    default: return state
  }
}

const persistedReducer = persistReducer(
  persistConfig, switchersReducer
);

// export const store = createStore(switchersReducer);
export const store = createStore(persistedReducer);
export const persistor = persistStore(store);

function App() {

  return (
    <Provider store={store}>
      <div className='wrap_total'>
        <div className='wrap_topmenu'>
          <p className='top_name'>Cyrillic reference book</p>
          <MainMenu />
        </div>
        <div className='wrap'>
          <div className='langmenu'>
            <Langlist />
          </div>
          <div className='container'>
            <ContentField />
          </div>
        </div>
        <div className='footer'>
          <p>Version 4.0</p>
          <p>&copy; Paratype, 2022</p>
        </div>
      </div>
    </Provider>
  );
}
export default App;



