import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import Legend from "./Legend";
import PanCyrillicLetters from "./PanCyrillicLetters";


const PanCyrillic = () => {
    const upcase = useSelector(state => state.upcase);
    const panCyrSort = useSelector(state => state.panCyrSort);

    const [alltable, setAllTable] = useState([]);
    const [alltableUni, setAllTableUni] = useState([]);

    useEffect(() => {
        let lng = 'https://raw.githubusercontent.com/typedev/cyrillic_lib/master/cyrillic-languages/site/cyrillic_characters_lib.json';
        fetch(lng)
            .then(res => res.json())
            .then(data => {
                setAllTable(data);
            })
        // console.log('Data ', alltable);
        // console.log('Initial ', alltable.uppercase_unicodes_list);
    }, []);

    const showUniTable = () => {
        let tmpnames = {};
        if (alltable?.uppercase_unicodes_list && alltable?.lowercase_unicodes_list) {
            tmpnames = alltable;
            tmpnames.uppercase_unicodes_list.sort((a, b) => a.unicodes > b.unicodes ? 1 : -1);
            tmpnames.lowercase_unicodes_list.sort((a, b) => a.unicodes > b.unicodes ? 1 : -1);
        }
        return tmpnames
    }

    // console.log('alltable', alltable);
    // console.log('unicodes', showUniTable())

    return (<>
        <p className='langtitle'>Pan-Cyrillic Character set</p>
        <Legend showSort />
        <p>&nbsp;</p>
        <div className={`lettersblock${panCyrSort ? '' : ' hidden'}`}>
            <div className={`lettersblock${upcase ? '' : ' hidden'}`}>
                <div className='flexcontainer'>
                    <PanCyrillicLetters alpahabet={alltable.uppercase_unicodes_list} />
                </div>
            </div>
            <div className={`lettersblock${!upcase ? '' : ' hidden'}`}>
                <div className='flexcontainer'>
                    <PanCyrillicLetters alpahabet={alltable.lowercase_unicodes_list} />
                </div>
            </div>
        </div>
        <div className={`lettersblock${!panCyrSort ? '' : ' hidden'}`}>
            <div className={`lettersblock${upcase ? '' : ' hidden'}`}>
                <div className='flexcontainer'>
                    <PanCyrillicLetters alpahabet={alltable.uppercase_sorted_by_unicodes} />
                </div>
            </div>
            <div className={`lettersblock${!upcase ? '' : ' hidden'}`}>
                <div className='flexcontainer'>
                    <PanCyrillicLetters alpahabet={alltable.lowercase_sorted_by_unicodes} />
                </div>
            </div>
        </div>
    </>)

};

export default PanCyrillic;