import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

const LanglistItem = ({ item }) => {
    const rus = useSelector(state => state.rus);
    const currentLang = useSelector(state => state.currentLang);
    const currentPage = useSelector(state => state.currentPage);
    const highlightedLang = useSelector(state => state.highlightedLang);
    const highlightedSelect = useSelector(state => state.highlightedSelect);
    const dispatch = useDispatch();

    const handleChangeCurrentLang = () => {
        if (currentLang !== item.name_eng) {
            dispatch({ type: 'switchCurrentLang', payload: item.name_eng });
        } else dispatch({ type: 'switchCurrentLang', payload: null });
    };

    const tempBorderColor = () => {
        let langid = highlightedLang?.find(lang => lang.name === item.name_eng);
        let langid_s = highlightedSelect?.languages?.find(lang => lang.name === item.name_eng);
        if ((langid && langid_s) && (currentLang != item.name_eng)) return ' listbg_on listborder_on'
        else if ((langid_s) && (currentLang != item.name_eng)) return ' listbg_on'
        else if ((langid) && (currentLang != item.name_eng)) return ' listborder_on'
        // else if (currentPage != 1) return ''
        else return ''
    }

    return (
        <button className={`langlistbutton${(currentLang === item.name_eng) ? ' langlistbuttonactive' : ''} ${(currentPage == 1) ? tempBorderColor() : ''}`} onClick={handleChangeCurrentLang}>{rus ? item.name_rus : item.name_eng}</button>
    )
};


const Langlist = () => {
    const [langlistnames, setLangListNames] = useState([]);

    const rus = useSelector(state => state.rus);
    const cpage = useSelector(state => state.currentPage);

    const titlebtnRuEn = ['RUS', 'RUS', 'ENG', 'ENG'];
    const dispatch = useDispatch();

    const handleChangerus = () => {
        dispatch({ type: 'toggleRuEn' });
    };

    // https://raw.githubusercontent.com/typedev/cyrillic_lib/master/cyrillic-languages
    // https://raw.githubusercontent.com/paratype/paratype.github.io/main/cyrillic-languages

    useEffect(() => {
        let lng = 'https://raw.githubusercontent.com/paratype/paratype.github.io/main/cyrillic-languages/cyrillic_library.json'
        fetch(lng)
            .then(res => res.json())
            .then(data => {
                setLangListNames(data);
            })
    }, []);

    const showLangList = () => {
        let tmpnames = langlistnames;
        if (rus) return tmpnames.sort((a, b) => a.name_rus > b.name_rus ? 1 : -1);
        else return tmpnames.sort((a, b) => a.name_eng > b.name_eng ? 1 : -1);
    }

    return (
        <>
            {/* <ToggleButtons titles={titlebtnRuEn} switcher={rus} handleClick={handleChangerus} /> */}
            {showLangList().map(item => {
                return (
                    item.enable ?
                        <LanglistItem key={item.name_eng} item={item} /> :
                        <></>
                )
            })
            }
        </>
    );
};

export default Langlist;
